* {
  padding: 0;
  margin: 0;
  font-size: 1em;
  outline: none;
  scroll-behavior: smooth;
  font-family: -apple-system, BlinkMacSystemFont, sans-serif;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.burger-icon {
  display: none;
}

/* Firefox */
input[type='number'] {
  -moz-appearance: textfield;
}

div {
  box-sizing: border-box;
}

body {
  background-color: #fff;
}

a {
  color: #000;
  text-decoration: none;
}

li {
  list-style: none;
}

.sign__submit-btn__dark {
  cursor: pointer;
  width: 100%;
  border: none;
  background-color: rgba(0, 0, 0, 0.9);
  height: 45px;
  color: rgba(255, 255, 255, 0.9);
  border-radius: 5px;
  font-size: 0.8em;
  transition: 0.2s linear;
}

.sign__submit-btn__light {
  cursor: pointer;
  width: 100%;
  border: none;
  background-color: rgba(255, 255, 255, 1);
  height: 45px;
  border: 2px solid #fff;
  color: rgba(0, 0, 0, 0.9);
  border-radius: 5px;
  font-size: 0.8em;
  transition: 0.2s linear;
}

.sign__submit-btn__dark:hover {
  cursor: pointer;
  transition: 0.2s linear;
  background-color: rgb(14, 69, 105);
}

.sign__submit-btn__dark:active {
  transition: 0.2s linear;
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
}

.sign__submit-btn__light:hover {
  cursor: pointer;
  transition: 0.2s linear;
  border: 2px solid rgba(0, 0, 0, 0.9);
}

.sign__submit-btn__light:active {
  transition: 0.2s linear;
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
}

select {
  width: 100%;
  min-width: 15ch;
  max-width: 30ch;
  border: none;
  border-radius: 0.25em;
  padding: 0.25em 0.5em;
  font-size: 1em;
  cursor: pointer;
  font-weight: bold;
  line-height: 1.1;
  background-color: #fff;
  background-image: linear-gradient(to top, #f9f9f9, #fff 33%);
}

/* Footer styles */
.footer {
  margin-top: 100px;
  height: 220px;
  display: flex;
  align-items: flex-start;
  justify-content: space-around;
  color: #fff;
  padding-top: 40px;
  background-color: rgba(0, 0, 0, 0.85);
}

.footer__col-1 {
  display: flex;
  flex-direction: column;
}

.footer__col-1__logo {
  font-size: 1.3em;
  color: rgba(255, 255, 255, 0.95);
}

.footer__col-1__copyright {
  margin-top: 5px;
  color: rgba(255, 255, 255, 0.55);
  font-size: 0.9em;
}

.footer__col-1__social {
  margin-top: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 30%;
}

.footer__col-1__social * {
  color: rgba(255, 255, 255, 0.95);
}

.footer__col-1__menu {
  margin-top: 20px;
}

.footer__col-1__menu * {
  color: rgba(255, 255, 255, 0.3);
}

.footer__col-1__menu ul a li {
  margin-top: 5px;
  transition: 0.2s linear;
}

.footer__col-1__menu ul a:hover li {
  color: rgba(255, 255, 255, 0.9);
  transition: 0.2s linear;
}

.footer__col-2 {
  display: flex;
  flex-direction: column;
}

.footer__col-2__logo {
  margin-bottom: 15px;
}

.footer__col-2__logo,
.footer__col-3__logo {
  font-size: 1.3em;
  color: rgba(255, 255, 255, 0.95);
}

.footer__col-2__phone-1,
.footer__col-2__phone-2 {
  color: rgba(255, 255, 255, 0.7);
  margin-top: 10px;
  transition: 0.2s linear;
}

.footer__col-2__phone-1:hover,
.footer__col-2__phone-2:hover {
  cursor: pointer;
  color: rgba(255, 255, 255, 0.95);
  transition: 0.2s linear;
}

.footer__col-2__email {
  margin-top: 25px;
  color: rgba(255, 255, 255, 0.7);
  transition: 0.2s linear;
}

.footer__col-2__email:hover {
  cursor: pointer;
  transition: 0.2s linear;
  color: rgba(255, 255, 255, 0.95);
}

.footer__col-3__menu {
  display: flex;
  align-items: center;
  margin-top: 15px;
}

.footer__col-3 ul {
  display: flex;
  flex-direction: column;
}

.footer__col-3 ul * {
  cursor: pointer;
  color: rgba(255, 255, 255, 0.5);
  transition: 0.2s linear;
}

.footer__col-3 ul a li {
  margin-top: 10px;
}

.footer__col-3 ul a:hover li {
  color: rgba(255, 255, 255, 0.95);
  transition: 0.2s linear;
}

.footer__col-3 ul:first-child {
  margin-right: 50px;
}

/* Style for all checkboxes */
input[type='checkbox'] {
  -webkit-filter: hue-rotate(210deg);
          filter: hue-rotate(210deg);
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

.search-error {
  font-size: 1.5em;
  color: rgba(0, 0, 0, 0.9);
}

.homeApp {
  height: 100vh;
  --main-text-color: rgba(255, 255, 255, 0.9);
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 3% 5%;
}

.header__logo-title {
  font-weight: bold;
  font-size: 1.5em;
  color: rgba(0, 0, 0, 0.85);
}

.header__logo-block,
.header__sign-btns-block {
  width: 25%;
}

.header__nav-block {
  width: 50%;
  display: flex;
  justify-content: center;
}

.header__main-menu {
  width: 100%;
}

.header__main-menu__ul {
  width: 100%;
  display: flex;
  justify-content: space-evenly;
}

.header__main-menu__ul a {
  color: rgba(0, 0, 0, 0.4);
  transition: 0.2s linear;
}

.header__main-menu__ul a:hover {
  color: rgba(0, 0, 0, 0.9);
  transition: 0.2s linear;
}

.search-icon {
  cursor: pointer;
  color: rgba(0, 0, 0, 0.4);
}

.log-in__btn,
.sign-up__btn {
  cursor: pointer;
  background: none;
  border: none;
  border-radius: 5px;
  padding: 10px 25px;
  transition: 0.2s linear;
  font-size: 0.8em;
}

.log-in__btn {
  margin-right: 10px;
}

.sign-up__btn {
  background-color: rgba(0, 0, 0, 0.9);
  color: rgba(255, 255, 255, 0.9);
}

.header__sign-btns-block {
  display: flex;
  justify-content: flex-end;
}

/* Head Booking section*/
.booking-block {
  margin-top: 60px;
  height: 75vh;
  margin: 0 5%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  border-radius: 20px;
  background: url(/react-booking/static/media/loginBg.9eecee60.jpg) no-repeat center top/cover;
}

.booking-block__title {
  font-size: 3em;
  color: var(--main-text-color);
}

.booking-block__head-text {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.booking-block__subtext {
  color: rgba(255, 255, 255, 0.7);
  margin-top: 30px;
}

.booking-block__form {
  width: 80%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 14px 30px;
  background-color: #fff;
  border-radius: 8px;
}

.select-label {
  color: rgba(0, 0, 0, 0.7);
  transition: 0.2s linear;
}

.select-block {
  transition: 0.2s linear;
  margin-left: 10px;
  font-weight: bold;
  border: none;
}

.booking-block__form-location {
  border-right: 1px solid rgba(0, 0, 0, 0.1);
  padding-right: 20px;
}

.booking-block__form-abode {
  border-left: 1px solid rgba(0, 0, 0, 0.1);
  padding-left: 20px;
}

.form-location__select,
.form-abode__select {
  display: flex;
  align-items: center;
  margin-top: 4px;
  height: 38px;
}

.location-icon,
.user-icon {
  color: rgba(0, 0, 0, 0.8);
}

.date-picker__from,
.date-picker__to {
  cursor: pointer;
  border: none;
  margin-left: 15px;
  font-weight: bold;
  padding-top: 2px;
  width: 100%;
}

.date-picker__to {
  margin-left: 0;
}

.booking-block__form-location {
  width: 24%;
}

.booking-block__form-checkIn,
.booking-block__form-checkOut {
  width: 15%;
}

.booking-block__form-abode {
  width: 15%;
}

.search-btn__booking {
  width: 15%;
  margin-left: 10%;
}

.content-section {
  position: relative;
  min-height: 100vh;
  margin-top: 100px;
  padding: 0 5%;
  display: flex;
  justify-content: space-between;
  --main-text-color: rgba(255, 255, 255, 0.9);
  --black-style-color: rgba(0, 0, 0, 0.85);
}

.left-content {
  width: 65%;
}

.right-sidebar {
  width: 25%;
}

.left-content__search-block {
  display: flex;
  justify-content: space-between;
}

.left-content__search {
  width: 60%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 5px;
  border: 2px solid rgba(0, 0, 0, 0);
  transition: 0.2s linear;
  background-color: rgba(0, 0, 0, 0.05);
}

.left-content__search:focus-within {
  border: 2px solid rgba(0, 0, 0, 0.5);
  box-shadow: 0px 0px 12px -2px rgba(34, 60, 80, 0.3);
  transition: 0.2s linear;
}

.search-icon {
  width: 8%;
}

.search-input {
  width: 91%;
  padding: 15px 0;
  border: none;
  padding-right: 15px;
  background-color: transparent;
}

.left-content__sort-btn {
  width: 38%;
  background-color: rgba(0, 0, 0, 0.9);
  border-radius: 5px;
  padding: 0 15px;
}

.left-content__sort-btn {
  display: flex;
  align-items: center;
}

.sort-btn__text {
  color: rgba(255, 255, 255, 0.7);
  font-size: 0.9em;
}

.left-content__sort-btn {
  justify-content: space-evenly;
}

.left-content__sort-btn .select-block {
  font-size: 0.9em;
  width: 60%;
  background: none;
  color: rgba(255, 255, 255, 0.85);
}

.left-content__sort-btn .select-block option {
  color: #000;
}

/* Apartments section */
.apartments-block {
  margin-top: 30px;
  margin-bottom: 15px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.apartments-block__item {
  position: relative;
  width: 30%;
  margin: 30px 0;
  transition: 0.2s linear;
}

.apartments-block__item:hover {
  cursor: pointer;
  transition: 0.2s linear;
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
  box-shadow: 1px 17px 8px 0px rgba(34, 60, 80, 0.2);
}

.apartments-block__item * {
  color: rgba(0, 0, 0, 0.9);
}

.apartments-block__item-img {
  height: 180px;
}

.apartments-block__item-img img {
  width: 100%;
  height: 180px;
  border-top-right-radius: 8px;
  border-top-left-radius: 8px;
}

.apartments-block__props {
  height: 120px;
  padding: 15px 25px;
  background-color: rgba(0, 0, 0, 0.05);
}

.apartments-block__props-location {
  margin-top: 8px;
}

.apartments-block__props-rating {
  margin-top: 10px;
}

.apartments-block__props-location {
  display: flex;
  align-items: center;
}

.apartments-block__props-location__text {
  margin-left: 10px;
}

.apartments-block__price-absolute {
  position: absolute;
  z-index: 11;
  padding: 6px 25px;
  font-size: 0.8em;
  right: 0;
  top: 30px;
  background-color: #fff;
}

.apartments-block__price-value {
  font-weight: bold;
}

.rating-icon {
  font-size: 0.8em;
}

.apartments-block__props-rating {
  width: 40%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.pagination {
  width: 40%;
  margin-top: 30px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.pagination__prev,
.pagination__next {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0.05);
  padding: 10px 12px;
  border-radius: 50%;
  transition: 0.2s linear;
}

.pagination__prev:hover,
.pagination__next:hover {
  background-color: rgba(0, 0, 0, 0.9);
  color: rgba(255, 255, 255, 0.9);
  transition: 0.2s linear;
}

.pagination__prev:active,
.pagination__next:active {
  transition: 0.2s linear;
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
}

.pagination-page {
  transition: 0.2s linear;
  background-color: transparent;
  padding: 10px 15px;
  border-radius: 50%;
}

.pagination-page:hover {
  cursor: pointer;
  transition: 0.2s linear;
  background-color: rgba(0, 0, 0, 0.9);
  color: rgba(255, 255, 255, 0.9);
}

/* Right sidebar styles */
.map-block {
  display: flex;
  flex-direction: column;
}

.map-block__title,
.price-block__title,
.rooms-block__title,
.facilities-block__title {
  font-size: 1.2em;
  font-weight: bold;
  color: var(--black-style-color);
}

.map-block__img {
  margin-top: 20px;
}

.price-block,
.facilities-block {
  margin-top: 80px;
}

.rooms-block {
  margin-top: 45px;
}

.price-block__inputs {
  display: flex;
  justify-content: space-between;
  margin-top: 30px;
}

.price-block__inputs .price-block__inputs-from,
.price-block__inputs .price-block__inputs-to {
  width: 150px;
  text-indent: 15px;
  padding: 10px 0;
  border: none;
  background-color: rgba(0, 0, 0, 0.05);
}

.rooms-block__toggles,
.beds-block__toggles,
.baths-block__toggles {
  margin-top: 40px;
  display: flex;
  flex-direction: column;
}

.rooms-block__toggles-room,
.beds-block__toggles-bed,
.baths-block__toggles-bath {
  display: flex;
  justify-content: space-between;
}

.rooms-block__toggles-room .toggles-room__toggles,
.beds-block__toggles-room .toggles-bed__toggles,
.baths-block__toggles-room .toggles-bath__toggles {
  display: flex;
}

.toggles-room__title,
.toggles-bed__title,
.toggles-bath__title {
  font-weight: bold;
  color: var(--black-style-color);
}

.toggles-room__toggles,
.toggles-bed__toggles,
.toggles-bath__toggles {
  width: 25%;
  display: flex;
  justify-content: space-between;
}

.toggles-room__toggles *,
.toggles-bed__toggles *,
.toggles-bath__toggles * {
  color: var(--black-style-color);
}

.toggles-minus,
.toggles-plus {
  cursor: pointer;
  transition: 0.2s linear;
}

.toggles-minus:hover,
.toggles-plus:hover {
  -webkit-transform: scale(1.3);
          transform: scale(1.3);
  transition: 0.2s linear;
}

.beds-block__toggles-bed,
.baths-block__toggles-bath {
  margin-top: 25px;
}

.rooms-block * {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
}

.facilities-block__items {
  margin-top: 35px;
}

.facilities-block__items__item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 20px;
}

.facilities-block__items__item__all-facilities {
  margin-top: 20px;
  display: flex;
  align-items: center;
  cursor: pointer;
}

.facilities-block__items__item__all-facilities:hover * {
  color: rgba(0, 0, 0, 0.85);
  transition: 0.2s linear;
}

.facilities-block__items__item__all-facilities:active * {
  transition: 0.2s linear;
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
}

.facilities-block__items__item__all-facilities * {
  color: rgba(0, 0, 0, 0.5);
  transition: 0.2s linear;
}

.arrow-right {
  margin-left: 15px;
  font-size: 0.8em;
  padding-top: 1px;
}

.clear-btn {
  width: 100%;
  padding: 10px 0;
  border: none;
  transition: 0.2s linear;
  border: 2px solid transparent;
  border-radius: 5px;
  margin-top: -30px;
  margin-bottom: 30px;
}

.clear-btn:hover {
  cursor: pointer;
  border: 2px solid rgba(0, 0, 0, 0.5);
  box-shadow: 0px 0px 12px -2px rgba(34, 60, 80, 0.3);
  transition: 0.2s linear;
}

.clear-btn:active {
  transition: 0.2s linear;
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
}

.loginApp {
  display: flex;
  height: 100vh;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.9);
}

.login-form {
  width: 70%;
  height: 90%;
  display: flex;
  border-radius: 20px;
}

.login-form__left-bar {
  width: 40%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #fff;
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px;
}

.login-form__inputs {
  width: 70%;
  height: 40%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  margin-top: 50px;
}

.login-form__inputs-email-block label,
.login-form__inputs-password-block label {
  color: rgba(0, 0, 0, 0.3);
  font-weight: bold;
  margin-bottom: 5px;
}

.login-form__inputs-email-block,
.login-form__inputs-password-block {
  display: flex;
  flex-direction: column;
}

.login-form__title {
  font-size: 1.5em;
  color: rgba(0, 0, 0, 0.8);
  font-weight: bold;
}

.login-form__preview {
  width: 60%;
  display: flex;
  align-items: center;
  justify-content: center;
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
  background: url(/react-booking/static/media/loginBg.9eecee60.jpg) no-repeat center top/cover;
}

.login-form__preview * {
  color: rgba(255, 255, 255, 0.9);
}

.login-form__preview-head {
  width: 50%;
  margin-right: 100px;
}

.login-form__preview h1 {
  font-size: 3em;
}

.login-form__preview-head__text {
  margin-top: 10px;
  color: rgba(255, 255, 255, 0.7);
}

#login-form__inputs-email,
#login-form__inputs-password {
  padding: 8px 0;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  text-indent: 15px;
  transition: 0.2s linear;
}

#login-form__inputs-email:focus,
#login-form__inputs-password:focus {
  transition: 0.2s linear;
  border: 1px solid rgba(0, 0, 0, 0.8);
}

.login-form__submit-btn {
  width: 100%;
  border: none;
  background-color: rgba(0, 0, 0, 0.9);
  height: 45px;
  color: rgba(255, 255, 255, 0.9);
  border-radius: 5px;
  font-size: 0.8em;
  transition: 0.2s linear;
}

.login-form__submit-btn:hover {
  cursor: pointer;
  transition: 0.2s linear;
  background-color: rgb(14, 69, 105);
}

.login-form__submit-btn:active {
  transition: 0.2s linear;
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
}

.login-form__remember-block {
  display: flex;
  justify-content: space-between;
}

.login-form__remember-block__rememb {
  display: flex;
  align-items: center;
}

.remember-text {
  color: rgba(0, 0, 0, 0.3);
  font-weight: bold;
  font-size: 0.8em;
  padding-left: 8px;
}

.forgot-text {
  color: rgba(0, 0, 0, 0.9);
  font-weight: bold;
  font-size: 0.8em;
  transition: 0.2s linear;
}

.forgot-text:hover {
  color: rgb(44, 93, 139);
}

.forgot-link {
  text-decoration: none;
}

.loader {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 45px;
}

@media (max-width: 768px) {
  .login-form {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    border-radius: 20px;
    align-items: center;
  }

  .login-form__left-bar {
    width: 100%;
    height: 50vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    background-color: #fff;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
    order: 2;
  }

  .login-form__preview {
    width: 100%;
    height: 50vh;
    display: flex;
    align-items: center;
    justify-content: center;
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
    background: url(/react-booking/static/media/loginBg.9eecee60.jpg) no-repeat center top/cover;
  }

  .login-form__preview * {
    color: rgba(255, 255, 255, 0.9);
  }

  .login-form__preview-head {
    width: 90%;
    margin-right: 0px;
  }

  .login-form__preview h1 {
    font-size: 3em;
  }

  .login-form__inputs {
    width: 80%;
    height: 80%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
